export default {
  name: "Header",
  data() {
    return {
      dataLogged: null,
      isMenuOpen: false,
    }
  },
  mounted() {
    this.dataLogged = (this.$route.query.logged) ? this.$route.query.logged : null;
  },
  methods: {
    goToHome() {
      window.location.href = '/';
    },
    openMenu() {
      this.isMenuOpen = true;
    },
    closeMenu() {
      this.isMenuOpen = false;
    }
  }
};
